export const formatDate = (releaseDate) => {
    if (!releaseDate) return "Unknown Date"; // If releaseDate is null or undefined
  
    const { day, month, year } = releaseDate;
    const validDay = day && day > 0 && day <= 31 ? day.toString().padStart(2, '0')  : null;
    const validMonth = month && month > 0 && month <= 12 ? month.toString().padStart(2, '0') : null;
    const validYear = year ? year : null;
  
    // Construct the date string based on available parts
    if (validDay && validMonth && validYear) {
      return  `${validDay}/${validMonth}/${validYear}`;
    } else if (validMonth && validYear) {
      return `${validMonth}/${validYear}`;
    } else if (validYear) {
      return `${validYear}`;
    } else {
      return "Unknown Date";
    }
  };