import React, { useState, useEffect } from "react";
import Search from "../components/search";
import { Theme } from "@radix-ui/themes";
import "@radix-ui/themes/styles.css";
import MovieCard from "../components/movieCard";
import useLocalStorageState from "../data/useLocalStorageState";
import { formatDate } from '../utilis/formatDate';



const Main = ({ selectedOption }) => {
  const [localFavorites, setLocalFavorites] = useLocalStorageState("localFavorites", []);
  const [search, setSearch] = useState("");
  const [options, setOptions] = useState([]);
  const [selectedMovie, setSelectedMovie] = useState(null);
  const [movieData, setMovieData] = useState(null);
  const [defaultOptions, setDefaultOptions] = useState([]);

  useEffect(() => {
    const fetchMovies = async () => {
      if (search) {
        const url = `https://moviesdatabase.p.rapidapi.com/titles/search/keyword/${search}`;
        const options = {
          method: "GET",
          headers: {
            "x-rapidapi-key": "d1e04a830amsh4371c46c1af5845p1526aajsn58ae26c6daab",
            "x-rapidapi-host": "moviesdatabase.p.rapidapi.com",
          },
        };
  
        try {
          const response = await fetch(url, options);
          if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
          }
          const result = await response.json();
          console.log("Search Results:", result);
  
          if (result.results) {
            const newOptions = result.results.map((item) => ({
              id: item.id,
              movie: item.titleText?.text || "Unknown Title",
              type:item.titleType?.text || "Unknown",
              img: item.primaryImage?.url || "placeholder.jpg",
              releaseDate: {
                day: item.releaseDate?.day || null,
                month: item.releaseDate?.month || null,
                year: item.releaseDate?.year || null,
              },
            }));
  
            setDefaultOptions(newOptions);
            setOptions(newOptions);
          } else {
            console.error("No results found");
            setOptions([]);
          }
        } catch (error) {
          console.error("Error fetching movies:", error);
        }
      } else {
        setOptions([]);
        setDefaultOptions([]);
      }
    };
  
    fetchMovies();
  }, [search]);

  useEffect(() => {
    const fetchMovie = async () => {
      if (selectedMovie && selectedMovie.id) {
        const url = `https://moviesdatabase.p.rapidapi.com/titles/${selectedMovie.id}`;
        const options = {
          method: "GET",
          headers: {
            "x-rapidapi-key": "d1e04a830amsh4371c46c1af5845p1526aajsn58ae26c6daab",
            "x-rapidapi-host": "moviesdatabase.p.rapidapi.com",
          },
        };
  
        try {
          const response = await fetch(url, options);
          if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
          }
          const result = await response.json();
          console.log("Fetched Movie Data:", result);
          setMovieData(result.results);
        } catch (error) {
          console.error("Error fetching movie details:", error);
          setMovieData(null); // Ensure movieData is cleared in case of an error
        }
      }
    };
  
    fetchMovie();
  }, [selectedMovie]);

  useEffect(() => {
    const sortOptions = () => {
      let sortedOptions = [...defaultOptions];

      switch (selectedOption) {
        case "Alphabet":
          sortedOptions.sort((a, b) => a.movie.localeCompare(b.movie));
          break;
        case "Date":
          sortedOptions.sort((a, b) => new Date(b.releaseDate) - new Date(a.releaseDate));
          break;
        case "Ratings":
          // Assuming you have rating data to sort by, replace with appropriate field
          sortedOptions.sort((a, b) => (b.rating || 0) - (a.rating || 0));
          break;
        default:
          break;
      }

      setOptions(sortedOptions);
    };

    sortOptions();
  }, [selectedOption, defaultOptions]);

  const onInputChange = (event) => {
    const value = event.target.value;
    setSearch(value);
    setSelectedMovie(null); // Reset selectedMovie when input changes

    if (value) {
      const filteredOptions = defaultOptions.filter((option) =>
        option.movie.toLowerCase().includes(value.toLowerCase())
      );
      setOptions(filteredOptions);
    } else {
      setOptions([]);
      setMovieData(null); // Clear movieData when search input is empty
    }
  };

  const toggleFavoriteMovie = (movie) => {
    if (movie && movie.id) {
      const movieId = movie.id;
      const updatedFavorites = localFavorites.includes(movieId)
        ? localFavorites.filter((id) => id !== movieId)
        : [...localFavorites, movieId];
      
      setLocalFavorites(updatedFavorites);
    }
  };

  const onMovieSelected = (movie) => {
    setSelectedMovie(movie); //set movie selections
    setMovieData(null); // Clear previous movie data when a new movie is selected
  };

  return (
    <Theme>
      <div className="App container mt-2 mb-3">
        <Search
          options={options}
          onInputChange={onInputChange}
          movieSelected={onMovieSelected}
          toggleFavoriteMovie={toggleFavoriteMovie}
        />
<div className="cards-container">
  {selectedMovie ? (
    movieData ? (
      <MovieCard
        movieName={movieData.titleText?.text || "Unknown Title"}
        id={movieData.id || "Unknown ID"}
        img={movieData.primaryImage?.url || "placeholder.jpg"}
        isFavorites={localFavorites.includes(movieData.id)}
        date={formatDate(movieData.releaseDate)}
        type={movieData.titleType?.text || "Unknown"}
      />
    ) : (
      <p>Loading movie details...</p>
    )
  ) : options.length > 0 ? (
    options.map((movie) => (
      <MovieCard
        key={movie.id}
        movieName={movie.movie}
        id={movie.id}
        img={movie.img}
        isFavorites={localFavorites.includes(movie.id)}
        date={formatDate(movie.releaseDate)} 
        type={movie.type || "Unknown"}
        />
    ))
  ) : (
    <p></p>
  )}
</div>
        {!selectedMovie && !options.length && (
          <div className="no-selection-container">
            <p>No Movie or TV show selected</p>
          </div>
        )}
      </div>
    </Theme>
  );
};

export default Main;