import React, { useState, useEffect } from 'react';
import { Theme } from '@radix-ui/themes';
import useLocalStorageState from '../data/useLocalStorageState';
import MovieCard from '../components/movieCard';
import "@radix-ui/themes/styles.css";
import { formatDate } from '../utilis/formatDate';



const Favorite = ({ selectedOption }) => {
  const [localFavorites, setLocalFavorites] = useLocalStorageState('localFavorites');
  const [movieData, setMovieData] = useState([]);
  const [defaultOptions, setDefaultOptions] = useState([]);
  const [sortedMovies, setSortedMovies] = useState([]);


  useEffect(() => {
    const fetchMoviesForFavorites = async () => {
      try {
        if (localFavorites && localFavorites.length > 0) {
          // Ensure localFavorites is an array
          const parsedFavorites = Array.isArray(localFavorites) ? localFavorites : [];
          
          const promises = parsedFavorites.map(async (movieId) => {
            const url = `https://moviesdatabase.p.rapidapi.com/titles/${movieId}`;
            const options = {
              method: 'GET',
              headers: {
                'x-rapidapi-key': 'd1e04a830amsh4371c46c1af5845p1526aajsn58ae26c6daab',
                'x-rapidapi-host': 'moviesdatabase.p.rapidapi.com'
              }
            };

            const response = await fetch(url, options);
            if (!response.ok) {
              throw new Error('Failed to fetch movie data');
            }
            return response.json();
          });

          const data = await Promise.all(promises);
          setMovieData(data);
        }
      } catch (error) {
        console.error('Error fetching movie data:', error);
      }
    };

    fetchMoviesForFavorites();
  }, [localFavorites]);


  useEffect(() => {
    const sortMovies = () => {
      let sortedMovies = [...movieData];

      switch (selectedOption) {
        case "Alphabet":
          sortedMovies.sort((a, b) => a.results?.titleText?.text.localeCompare(b.results?.titleText?.text));
          break;
        case "Date":
          sortedMovies.sort((a, b) => new Date(b.results?.releaseDate?.year) - new Date(a.results?.releaseDate?.year));
          break;
        case "Ratings":
          // Assuming you have rating data to sort by, replace with appropriate field
          sortedMovies.sort((a, b) => (b.results?.rating || 0) - (a.results?.rating || 0));
          break;
        default:
          break;
      }

      setSortedMovies(sortedMovies);
    };

    sortMovies();
  }, [selectedOption, movieData]);

  return (
    <Theme>
      <div className="App container mt-2 mb-3">
        <div className="cards-container">
          {sortedMovies.length > 0 ? (
            sortedMovies.map((movie, index) => (
              <MovieCard
                movieName={movie.results?.titleText?.text || "Unknown Title"}
                id={movie.results?.id || "Unknown ID"}
                img={movie.results?.primaryImage?.url || "placeholder.jpg"}
                key={index}
                isFavorites={true}
                date={formatDate(movie.results?.releaseDate)}
                type={movie.results?.titleType?.text || "Unknown"}
              />
            ))
          ) : (
            <div className="no-selection-container-favorites">
              <p>No favorite movies found...</p>
            </div>
          )}
        </div>
      </div>
    </Theme>
  );
};

export default Favorite;