import React, { useState, useRef, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Theme, Button, TextField } from '@radix-ui/themes';
import { HeartIcon, MagnifyingGlassIcon, HeartFilledIcon } from '@radix-ui/react-icons';
import '@radix-ui/themes/styles.css';
import '../styles.css';
import { debounce } from '../utilis/debounce';

function Search(props) {
  const { options, onInputChange, movieSelected, toggleFavoriteMovie } = props;

  const ulRef = useRef(null);
  const inputRef = useRef();

  const [favoriteMovies, setFavoriteMovies] = useState([]);
  const [moviedetails, setMovieDetails] = useState(null);
  const [isFavorite, setIsFavorite] = useState(false);
  const [isButtonDisabled, setIsButtonDisabled] = useState(true);
  const [searchInput, setSearchInput] = useState('');

  useEffect(() => {
    if (localStorage.getItem("localFavorites")) {
      const storedFavorites = JSON.parse(localStorage.getItem("localFavorites"));
      setFavoriteMovies(storedFavorites);
    }
  }, []);

  useEffect(() => {
    setIsFavorite(isMovieInFavorites(moviedetails?.id));
  }, [moviedetails]);

  useEffect(() => {
    setIsButtonDisabled(!searchInput.trim() || !moviedetails || !moviedetails.movie);
  }, [searchInput, moviedetails]);

  const handleClick = (e) => {
    e.preventDefault();
    toggleFavoriteMovie(moviedetails);
    setIsFavorite(!isFavorite);
  };

  const isMovieInFavorites = (movieId) => {
    return favoriteMovies.includes(movieId);
  };

  const onInputChangeWrapper = (e) => {
    onInputChange(e);
    const inputValue = e.target.value;
    const movie = options.find((option) => option.movie.toLowerCase() === inputValue.toLowerCase());
    setMovieDetails(movie || null);
    setSearchInput(inputValue);
    setIsButtonDisabled(!inputValue.trim() || !movie);
  };

  const handleInputClick = (event) => {
    event.stopPropagation();
    if (options.length > 0) {
      ulRef.current.style.display = 'block';
    }
  };

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (ulRef.current && !ulRef.current.contains(event.target) && !inputRef.current.contains(event.target)) {
        ulRef.current.style.display = 'none';
      }
    };

    document.addEventListener('click', handleClickOutside);
    return () => {
      document.removeEventListener('click', handleClickOutside);
    };
  }, []);

  useEffect(() => {
    if (inputRef.current) {
      inputRef.current.addEventListener('click', handleInputClick);
    }
    return () => {
      if (inputRef.current) {
        inputRef.current.removeEventListener('click', handleInputClick);
      }
    };
  }, []);

  return (
    <Theme>
      <div className="search-button">
        <div className="search-bar-container">
          <TextField.Root
            placeholder="Search Movie/Show"
            autoComplete="true"
            ref={inputRef}
            onChange={debounce(onInputChangeWrapper, 200)}
            className="custom-textfield"
          >
            <TextField.Slot>
              <MagnifyingGlassIcon height="16" width="16" />
            </TextField.Slot>
          </TextField.Root>
          <Button onClick={handleClick} disabled={isButtonDisabled} className="favorite-button">
            <div>{isFavorite ? <HeartFilledIcon /> : <HeartIcon />}</div>
          </Button>
        </div>
        <ul
          id="results"
          className="list-group"
          ref={ulRef}
          style={{
            display: options.length > 0 ? 'block' : 'none',
            maxHeight: '200px',
            overflowY: 'auto',
            border: '1px solid #ddd',
            borderRadius: '4px',
            padding: '0',
            margin: '0',
            listStyleType: 'none',
            backgroundColor: '#fff',
            position: 'absolute',
            width: '100%',
            zIndex: 10
          }}
        >
          {options.length > 0 ? (
            options.map((option, index) => (
              <button
                type="button"
                key={index}
                onClick={(e) => {
                  inputRef.current.value = option.movie;
                  setMovieDetails(option);
                  movieSelected(option);
                  setIsButtonDisabled(false);
                  ulRef.current.style.display = 'none'; // Hide the list after selecting an option
                }}
                className="list-group-item list-group-item-action"
              >
                {option.movie}
              </button>
            ))
          ) : (
            <li className="list-group-item">No results found</li>
          )}
        </ul>
      </div>
    </Theme>
  );
}

export default Search;