import React, { useState, useEffect, useRef } from 'react';
import '@radix-ui/themes/styles.css';
import { Card } from '@radix-ui/themes';
import { Button } from '@radix-ui/themes';
import '../styles.css';
import { Tooltip } from 'react-tooltip';
import 'react-tooltip/dist/react-tooltip.css';
import { debounce } from '../utilis/debounce';

function MovieCard({ movieName, id, img, date,type }) {
  const [rating, setRating] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [isOverflowing, setIsOverflowing] = useState(false);
  const movieNameRef = useRef(null);
  const [validImg, setValidImg] = useState(true);

  useEffect(() => {
    const fetchRating = async () => {
      const url = `https://moviesdatabase.p.rapidapi.com/titles/${id}/ratings`;
      const options = {
        method: 'GET',
        headers: {
          'x-rapidapi-key': 'd1e04a830amsh4371c46c1af5845p1526aajsn58ae26c6daab',
          'x-rapidapi-host': 'moviesdatabase.p.rapidapi.com',
        },
      };
  
      try {
        const response = await fetch(url, options);
        const textResponse = await response.text(); // Get raw response text
        console.log('Raw response text:', textResponse);
  
        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }
        const data = JSON.parse(textResponse); // Parse the response as JSON
        console.log('Parsed API Response:', data);
  
        if (data && data.results) {
          console.log('Results:', data.results);
          setRating(data.results.averageRating || 'N/A');
        } else {
          setRating('N/A');
        }
      } catch (error) {
        console.error('Error fetching rating:', error);
        setRating('N/A');
      } finally {
        setLoading(false);
      }
    };
  
    fetchRating();
  }, [id]);

  useEffect(() => {
    const validateImage = async () => {
      if (img) {
        if (img.includes('placeholder.jpg')) {
          setValidImg(false);
        } else {
          try {
            const response = await fetch(img, { method: 'HEAD' });
            setValidImg(response.ok);
          } catch {
            setValidImg(false);
          }
        }
      } else {
        setValidImg(false);
      }
    };

    validateImage();
  }, [img]);

  const checkOverflow = () => {
    if (movieNameRef.current) {
      const isOverflow =
        movieNameRef.current.scrollWidth > movieNameRef.current.clientWidth;
      setIsOverflowing(isOverflow);
    }
  };

  useEffect(() => {
    checkOverflow();
    const handleResize = debounce(checkOverflow, 100);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, [movieName]);

  const tooltipId = `movie-tooltip-${id}`;

  return (
    <Card className="movie-card">
      <div className="movie-card-content">
        <div className="movie-image">
          {validImg ? (
            <img src={img} alt={movieName} />
          ) : (
            <div className="image-placeholder">No Image</div>
          )}
        </div>
        <div className="movie-details">
          <div
            className="movie-name-container"
            ref={movieNameRef}
            data-tooltip-id={isOverflowing ? tooltipId : undefined}
          >
            <div className="is-overflow-name">
              {isOverflowing ? "Hover to see full title" : "Title: "}
            </div>
            <div className="movie-name">{movieName}</div>
          </div>

          {isOverflowing && (
            <Tooltip id={tooltipId} place="top" content={movieName} className="custom-tooltip" />
          )}
          <div className="movie-date">Type: {type || 'N/A'}</div>
          <div className="movie-date">Release Date: {date || 'N/A'}</div>
          <div className="movie-rating">
            Rating: {loading ? 'Loading...' : rating}
          </div>
          <div className="movie-imdb-page">
            <a
              href={`https://www.imdb.com/title/${id}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              <Button className="imdb-location-button" variant="solid">
                IMDb Page
              </Button>
            </a>
          </div>
        </div>
      </div>
    </Card>
  );
}

export default MovieCard;