import React, { useState, useEffect } from "react";
import { BrowserRouter, Routes, Route, Link, useLocation, Navigate } from "react-router-dom";
import Main from "./Pages/main";
import Favorite from "./Pages/favorites";
import * as DropdownMenu from "@radix-ui/react-dropdown-menu";
import "./styles.css"; // Import your styles

function AppContent() {
  const [selectedOption, setSelectedOption] = useState("Default");
  const location = useLocation();

  // Effect to reset the selectedOption to 'Default' when navigating to the Main page
  useEffect(() => {
    if (location.pathname === "/") {
      setSelectedOption("Default");
    }
  }, [location]);

  return (
    <>
      <header className="header">
        <div className="header-left">
          <div>Movies Database</div>
        </div>
        <div className="header-right">
          <div className="radio-group">
            {/* Sorting Options */}
            <label>
              <input
                type="radio"
                value="Default"
                checked={selectedOption === "Default"}
                onChange={(e) => setSelectedOption(e.target.value)}
              />
              Default
            </label>
            <label>
              <input
                type="radio"
                value="Alphabet"
                checked={selectedOption === "Alphabet"}
                onChange={(e) => setSelectedOption(e.target.value)}
              />
              List by Alphabet
            </label>
            <label>
              <input
                type="radio"
                value="Date"
                checked={selectedOption === "Date"}
                onChange={(e) => setSelectedOption(e.target.value)}
              />
              List by Date
            </label>
            <label>
              <input
                type="radio"
                value="Ratings"
                checked={selectedOption === "Ratings"}
                onChange={(e) => setSelectedOption(e.target.value)}
              />
              List by Ratings
            </label>
          </div>

          <div className="dropdown-trigger">
            <DropdownMenu.Root>
              <DropdownMenu.Trigger>Choose Page</DropdownMenu.Trigger>
              <DropdownMenu.Portal>
                <DropdownMenu.Content>
                  <Link to="/">
                    <DropdownMenu.Item>Main</DropdownMenu.Item>
                  </Link>
                  <Link to="/Favorite">
                    <DropdownMenu.Item>Favorite List</DropdownMenu.Item>
                  </Link>
                </DropdownMenu.Content>
              </DropdownMenu.Portal>
            </DropdownMenu.Root>
          </div>
        </div>
      </header>

      <Routes>
        {/* Default Route to Main */}
        <Route path="/" element={<Main selectedOption={selectedOption} />} />
        <Route path="/Favorite" element={<Favorite selectedOption={selectedOption} />} />
        {/* Redirect all other routes to Main */}
        <Route path="*" element={<Navigate to="/" replace />} />
      </Routes>
    </>
  );
}

function App() {
  return (
    <BrowserRouter>
      <AppContent />
    </BrowserRouter>
  );
}

export default App;